<template>
  <div  class="login">
    <img src="/img/logo-memoire.png" class="logo" />
    <div class="error">{{message}}</div>
<input type="text" class="css-input" placeholder="Choisir un nom"
       v-on:input="setMessage($event)"
      v-on:keyup.enter="setId($event)"
    />

    
  </div>
</template>

<script>

export default {
  name: 'Login',



 
  computed:{
    id(){
      
     return this.$store.state.id;
    },
     message(){
      
     return this.$store.state.message;
    },
    

    
  },

methods:{  
 setId: function (e) {
    
       
   
     var re = new RegExp("^[a-zA-Z0-9]+$", "g");

        
              
   if (e.target.value.length<4) {
     this.$store.commit('setMessage', "minimum 4 lettres"); 

   }else if(!re.test(e.target.value)){
     this.$store.commit('setMessage', "uniquement des lettres et des chiffres"); 
    
   }else{
     this.$store.commit('setMessage', ""); 
       this.$store.commit('setId', 1);
       this.$router.push('/'); 
   }
     },
    setMessage: function (e) {
     
     var re = new RegExp("^[a-zA-Z0-9]+$", "g");
              
   if (e.target.value.length>=4) {
   
     this.$store.commit('setMessage', ""); 
       
   }
   if(!re.test(e.target.value)){
     this.$store.commit('setMessage', "uniquement des lettres et des chiffres"); 
  
    }
    if(re.test(e.target.value)){
     this.$store.commit('setMessage', ""); 
     
    
   }
         
        
        
          
          
        },
         
       
       },
        beforeMount(){
          if (this.id!=0) {
          this.$router.push('/');  
          }
         
        },
}


</script>
